<template>
  <span class="header__count" aria-label="Количество товаров">{{
    productsCount
  }}</span>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "CartIndicator",

  computed: {
    ...mapGetters(["productsCount", "cartLoaded"]),
  },
  methods: {
    ...mapActions(["getCartProducts"]),
  },
  created() {
    if (!this.cartLoaded) this.getCartProducts();
  },
};
</script>
