import Api from "@/helpers/api";
import debounce from "@/helpers/debounce";
import showHandledMessage from "@/helpers/show-handled-message";
import imgPlaceholder from "../../../public/img/product-placeholder.jpg";

let oldAmounts = [];

export default {
  state: {
    cartProducts: [],
    user: null,
    cartId: null,
    isCartLoaded: false,
    isCartLoading: false,
    isCartBlocked: false,
  },
  getters: {
    productsCount({ cartProducts }) {
      return cartProducts.length;
    },
    totalAmount({ cartProducts }) {
      return cartProducts.reduce((acc, curr) => {
        return acc + curr.price * curr.quantity;
      }, 0);
    },
    cartBlocked({ isCartBlocked }) {
      return isCartBlocked;
    },
    cartLoading({ isCartLoading }) {
      return isCartLoading;
    },
    cartLoaded({ isCartLoaded }) {
      return isCartLoaded;
    },
    productsForCartPage({ cartProducts }) {
      return cartProducts.length
        ? cartProducts.map((product) => ({
            color: product.color.color,
            imgUrl: product.color?.gallery?.length
              ? product.color.gallery[0].file.url
              : imgPlaceholder,
            id: product.product.id,
            price: product.price,
            itemId: product.id,
            amount: product.quantity,
            size: product.size,
            title: product.product.title,
            isLoading: product.isLoading,
          }))
        : [];
    },
  },
  mutations: {
    clearCart(state) {
      state.cartProducts = [];
    },
    setIsCartBlocked(state, isblocked) {
      state.isCartBlocked = isblocked;
    },
    setCartLoaded(state, isLoaded) {
      state.isCartLoaded = isLoaded;
    },
    setItemIsLoading(state, { id, isLoading }) {
      state.cartProducts.find((it) => it.id === id).isLoading = isLoading;
    },
    setCartIsLoading(state, isLoading) {
      state.isCartLoading = isLoading;
    },
    setProducts(state, products) {
      state.cartProducts = products.map((p) => ({ ...p, isLoading: false }));
    },
    setUser(state, newUser) {
      state.user = newUser;
    },
    setCartId(state, id) {
      state.cartId = id;
    },
    addProduct(state, product) {
      const finded = state.cartProducts.find(
        (it) =>
          it.product.id === product.product.id &&
          it.color.id === product.color.id &&
          it.size.id === product.size.id
      );
      if (finded) {
        finded.id = product.id;
        finded.quantity += product.quantity;
      } else {
        state.cartProducts.push(product);
      }
    },
    deleteProduct(state, id) {
      state.cartProducts = state.cartProducts.filter(
        (product) => product.id !== id
      );
    },
    updateAmount(state, { id, amount }) {
      const product = state.cartProducts.find((product) => product.id === id);
      if (product) {
        product.quantity = amount;
      }
    },
  },
  actions: {
    async getCartProducts({ commit }) {
      commit("setCartIsLoading", true);
      commit("setIsCartBlocked", true);
      try {
        const response = await Api.getCartProducts();
        commit("setProducts", response.data.items);
        commit("setUser", response.data.user);
        commit("setCartId", response.data.id);
        commit("setCartLoaded", true);
        return response;
      } finally {
        commit("setCartIsLoading", false);
        commit("setIsCartBlocked", false);
      }
    },

    async createCartProduct({ commit }, { product, colorId, sizeId, amount }) {
      const newProduct = {
        id: Date.now(),
        product,
        color: product.colors.find((it) => it.color.id === colorId),
        size: product.sizes.find((it) => it.id === sizeId),
        quantity: amount,
      };
      commit("addProduct", newProduct);
      try {
        const response = await Api.createCartProduct({
          productId: product.id,
          colorId,
          sizeId,
          amount,
        });
        commit("setProducts", response.data.items);
        showHandledMessage(this._vm, { code: "PRODUCT_ADD" });
      } catch (error) {
        commit("deleteProduct", newProduct.id);
        showHandledMessage(this._vm, error);
      }
    },

    async updateCartProductAmount({ commit, state }, { itemId, newAmount }) {
      oldAmounts.push(
        state.cartProducts.find((it) => it.id === itemId).quantity
      );
      commit("updateAmount", { id: itemId, amount: newAmount });
      debounce(async () => {
        try {
          await Api.updateCartProductAmount({ itemId, newAmount });
        } catch (error) {
          if (oldAmounts.length) {
            commit("updateAmount", {
              id: itemId,
              amount: oldAmounts.shift(),
            });
            oldAmounts.length = 0;
          }
          showHandledMessage(this._vm, error);
        }
      });
    },

    async deleteCartProduct({ commit }, { productId }) {
      commit("setItemIsLoading", { id: productId, isLoading: true });
      debounce(async () => {
        try {
          await Api.deleteCartProduct(productId);
          commit("deleteProduct", productId);
          showHandledMessage(this._vm, { code: "PRODUCT_DELETE" });
        } catch (error) {
          commit("setItemIsLoading", { id: productId, isLoading: false });
          showHandledMessage(this._vm, error);
        }
      });
    },
  },
};
