<template>
  <div>
    <BaseHeader />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'home' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> Оформление заказа </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title">Оформление заказа</h1>
        </div>
      </div>

      <section class="cart">
        <form
          class="cart__form form"
          action="#"
          method="POST"
          @submit.prevent="submitFormHandler"
        >
          <div class="cart__field">
            <div class="cart__data">
              <FormFieldInput
                title="ФИО"
                placeholder="Введите ваше полное имя"
                v-model="formData.name"
                :error="formError.name"
                :disabled="cartBlocked"
              />
              <FormFieldInput
                title="Адрес доставки"
                placeholder="Введите ваш адрес"
                v-model="formData.address"
                :error="formError.address"
                :disabled="cartBlocked"
              />
              <FormFieldInput
                title="Телефон"
                type="tel"
                placeholder="Введите ваш телефон"
                v-model="formData.phone"
                :error="formError.phone"
                :disabled="cartBlocked"
              />
              <FormFieldInput
                title="Email"
                type="email"
                placeholder="Введи ваш Email"
                v-model="formData.email"
                :error="formError.email"
                :disabled="cartBlocked"
              />
              <FormFieldTextarea
                title="Комментарий к заказу"
                placeholder="Ваши пожелания"
                v-model="formData.comment"
                :error="formError.comment"
                :disabled="cartBlocked"
              />
            </div>

            <div class="cart__options" v-if="productsCount">
              <h3 class="cart__title">Доставка</h3>
              <div class="with-item-preloader">
                <ul class="cart__options options" v-if="deliveries.length">
                  <li
                    class="options__item"
                    v-for="delivery in deliveries"
                    :key="delivery.title"
                  >
                    <label class="options__label">
                      <input
                        class="options__radio sr-only"
                        type="radio"
                        name="delivery"
                        :value="delivery.id"
                        v-model="deliveryTypeId"
                      />
                      <span class="options__value">
                        {{ delivery.title }}
                        <b v-if="delivery.price != 0"
                          >{{ delivery.price | numberFormat }} ₽</b
                        >
                        <b v-else>бесплатно</b>
                      </span>
                    </label>
                  </li>
                </ul>
                <BasePreloader v-if="deliveriesLoading" is-item-preloader />
              </div>

              <h3 class="cart__title" v-if="deliveryTypeId">Оплата</h3>
              <div class="with-item-preloader" v-if="deliveryTypeId">
                <ul
                  class="cart__options options"
                  v-if="payments.length && !paymentsLoading"
                >
                  <li
                    class="options__item"
                    v-for="payment in payments"
                    :key="payment.title"
                  >
                    <label class="options__label">
                      <input
                        class="options__radio sr-only"
                        type="radio"
                        name="payment"
                        :value="payment.id"
                        v-model="paymentTypeId"
                      />
                      <span class="options__value"> {{ payment.title }} </span>
                    </label>
                  </li>
                </ul>
                <BasePreloader
                  v-if="deliveryTypeId && paymentsLoading"
                  is-item-preloader
                />
              </div>
            </div>
          </div>

          <div class="cart__block">
            <CartOrderList
              :products="productsForCartPage"
              :is-loading="cartLoading"
            />
            <CartTotal
              :total-price="totalAmount + currentStoreDeliveryPrice"
              :delivery-title="currentStoreDelivery.title"
              :delivery-price="currentStoreDeliveryPrice"
              :amount="productsCount"
            />

            <button
              class="cart__button button button--primery"
              type="submit"
              :disabled="!productsCount || cartBlocked"
            >
              <span v-if="!isLoading">Оформить заказ</span>
              <span v-else>Оформляем..</span>
              <span class="btn-preloader" v-if="isLoading">
                <svg class="btn-preloader__svg" viewBox="0 0 64 64">
                  <circle
                    cx="32"
                    cy="32"
                    r="31"
                    fill="none"
                    stroke-width="2"
                  ></circle>
                </svg>
              </span>
            </button>
          </div>
          <div
            class="cart__error form__error-block"
            v-if="
              errorMessage ||
              formError.paymentTypeId ||
              formError.deliveryTypeId
            "
          >
            <h4>Заявка не отправлена!</h4>
            <p v-if="formError.deliveryTypeId">
              {{ formError.deliveryTypeId }}
            </p>
            <p v-if="formError.paymentTypeId">{{ formError.paymentTypeId }}</p>
            <p>
              {{ errorMessage }}
            </p>
          </div>
        </form>
      </section>
    </main>
    <BaseFooter />
  </div>
</template>

<script>
import Api from "@/helpers/api";
import CartTotal from "@/components/CartTotal.vue";
import BaseFooter from "@/components/BaseFooter.vue";
import BaseHeader from "@/components/BaseHeader.vue";
import BasePreloader from "@/components/BasePreloader.vue";
import FormFieldInput from "@/components/FormFieldInput.vue";
import FormFieldTextarea from "@/components/FormFieldTextarea.vue";
import CartOrderList from "@/components/CartOrderList.vue";
import numberFormat from "@/helpers/number-format";
import normalizeWord from "@/helpers/normalise-word";
import showHandledMessage from "@/helpers/show-handled-message";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "OrderPage",
  components: {
    BaseHeader,
    BaseFooter,
    BasePreloader,
    FormFieldInput,
    FormFieldTextarea,
    CartOrderList,
    CartTotal,
  },
  data() {
    return {
      formData: {},
      formError: {},
      errorMessage: "",
      deliveryTypeId: 0,
      paymentTypeId: 0,
      isLoading: false,
    };
  },
  methods: {
    ...mapActions(["getPaymentsByDelivery", "getDeliveries"]),
    async submitFormHandler() {
      const data = {
        name: this.formData.name,
        email: this.formData.email,
        phone: this.formData.phone,
        address: this.formData.address,
        comment: this.formData.comment,
        deliveryTypeId: this.deliveryTypeId,
        paymentTypeId: this.paymentTypeId,
      };

      this.isLoading = true;
      try {
        const response = await Api.createOrder(data);
        this.formData = {};
        this.formError = {};
        this.errorMessage = "";
        this.$store.commit("setOrderInfo", response.data);
        this.$store.commit("clearCart");
        this.$router.push(`/orders/${response.data.id}`);
      } catch (error) {
        this.formError = error?.response?.data?.error?.request || {};
        this.errorMessage = error?.response?.data?.error?.message || "";
        showHandledMessage(this, error);
      } finally {
        this.isLoading = false;
      }
    },
  },
  computed: {
    ...mapGetters([
      "productsCount",
      "totalAmount",
      "cartLoading",
      "cartLoaded",
      "productsForCartPage",
      "cartBlocked",
      "deliveriesLoading",
      "deliveriesBlocked",
      "deliveries",
      "paymentsLoading",
      "paymentsBlocked",
      "payments",
    ]),
    currentStoreDelivery() {
      return (
        this.deliveries.find((it) => it.id === this.deliveryTypeId) || {
          price: 0,
        }
      );
    },
    currentStoreDeliveryPrice() {
      return Number(this.currentStoreDelivery.price);
    },
    currentStoreDeliveryTitle() {
      return this.currentStoreDelivery.title;
    },
  },
  filters: {
    numberFormat,
    normalizeWord,
  },
  watch: {
    deliveryTypeId(value, oldValue) {
      if (value > 0) {
        this.getPaymentsByDelivery({
          id: this.deliveryTypeId,
        })
          .then(() => {
            if (this.payments.length) this.paymentTypeId = this.payments[0].id;
          })
          .catch(() => {
            this.deliveryTypeId = oldValue;
          });
      }
      this.paymentTypeId = 0;
    },
  },
  created() {
    const loadDeliveries = () => {
      if (!this.deliveries.length) {
        this.getDeliveries().then(() => {
          if (this.deliveries.length)
            this.deliveryTypeId = this.deliveries[0].id;
        });
      } else {
        this.deliveryTypeId = this.deliveries[0].id;
      }
    };
    loadDeliveries();
  },
};
</script>
