<template>
  <form
    class="limit-controls"
    :class="className"
    action="#"
    method="GET"
    @submit.prevent="() => {}"
  >
    <h2 class="limit-controls__title">Показывать по:</h2>
    <label
      class="limit-controls__label"
      v-for="(valueItem, index) in values"
      :key="index"
      :class="{ 'limit-controls__label--current': value === valueItem }"
    >
      <input
        type="radio"
        class="limit-controls__radio visually-hidden"
        name="pagecount"
        v-model="currentPagesCount"
        :value="valueItem"
      />
      <span class="limit-controls__text">{{ valueItem }}</span>
    </label>
  </form>
</template>

<script>
export default {
  name: "LimitControls",

  props: ["value", "className"],
  computed: {
    values() {
      return [12, 24, 32];
    },
    currentPagesCount: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit("input", newValue);
      },
    },
  },
};
</script>

