    &::before {
      content: "";
      z-index: -1;
      position: absolute;
      left: -1px;
      top: -1px;
      border-bottom-right-radius: 15px;
      display: block;
      background-color: rgba(255, 255, 255, 0.97);
      width: 100%;
      height: 100%;
    }
  }

  &__link {
    margin-bottom: 5px;
    height: auto;
    width: 100%;
  }

  &__img {
    height: 250px;
    width: 100%;
  }

  &__colors {
    margin-top: auto;
    margin-bottom: 5px;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
  }

  &__btn {
    align-self: flex-end;
    border: none;
    position: relative;
    padding: 0;
    width: 32px;
    height: 36px;
    background-color: transparent;
    transition: 0.25s ease-in color, 0.25s ease-in opacity;
    cursor: pointer;

    &:focus-visible:not(:active),
    &:hover:not(:focus):not(:active) {
      color: #e02d71;
    }
    &:active {
      opacity: 0.5;
    }
    &:disabled {
      opacity: 0.2;
