export default function phoneNumberFormat(phoneNumber) {
  const numericPhoneNumber = phoneNumber.replace(/\D/g, "");
  const areaCode = numericPhoneNumber.slice(1, 4);
  const firstPart = numericPhoneNumber.slice(4, 7);
  const secondPart = numericPhoneNumber.slice(7, 9);
  const thirdPart = numericPhoneNumber.slice(9);
  return `+7 ${areaCode} ${firstPart} ${secondPart} ${thirdPart}`;
}
