import Api from "@/helpers/api";
import showHandledMessage from "@/helpers/show-handled-message";

export default {
  state: {
    isDeliveriesLoading: false,
    isDeliveriesBlocked: false,
    deliveriesData: [],
    isPaymentsLoading: false,
    isPaymentsBlocked: false,
    paymentsData: [],
    orderInfoData: null,
    isOrderInfoLoading: false,
  },
  getters: {
    orderInfo(state) {
      return state.orderInfoData;
    },
    orderInfoLoading(state) {
      return state.isOrderInfoLoading;
    },
    orderInfoProducts(state) {
      return Array.isArray(state?.orderInfoData?.basket?.items)
        ? state.orderInfoData.basket.items.map((product) => ({
            color: product.color.color,
            id: product.product.id,
            price: product.price,
            itemId: product.id,
            amount: product.quantity,
            size: product.size,
            title: product.product.title,
          }))
        : [];
    },
    orderInfoProductsCount({ orderInfoData }) {
      return orderInfoData?.basket?.items?.length;
    },
    orderInfoDelivery({ orderInfoData }) {
      return orderInfoData?.deliveryType;
    },
    deliveriesLoading({ isDeliveriesLoading }) {
      return isDeliveriesLoading;
    },
    deliveriesBlocked({ isDeliveriesBlocked }) {
      return isDeliveriesBlocked;
    },
    deliveries({ deliveriesData }) {
      return deliveriesData;
    },
    paymentsLoading({ isPaymentsLoading }) {
      return isPaymentsLoading;
    },
    paymentsBlocked({ isPaymentsBlocked }) {
      return isPaymentsBlocked;
    },
    payments({ paymentsData }) {
      return paymentsData;
    },
  },
  mutations: {
    setOrderInfoLoading(state, isLoading) {
      state.isOrderInfoLoading = isLoading;
    },
    setOrderInfo(state, data) {
      state.orderInfoData = data;
    },
    setIsDeliveriesLoading(state, isloading) {
      state.isDeliveriesLoading = isloading;
    },
    setIsDeliveriesBlocked(state, isBlocked) {
      state.isDeliveriesBlocked = isBlocked;
    },
    setDeliveries(state, data) {
      state.deliveriesData = data;
    },
    setIsPaymentsLoading(state, isLoading) {
      state.isPaymentsLoading = isLoading;
    },
    setIsPaymentsBlocked(state, isBlocked) {
      state.isPaymentsBlocked = isBlocked;
    },
    setPayments(state, data) {
      state.paymentsData = data;
    },
  },
  actions: {
    async getDeliveries({ commit }) {
      commit("setIsDeliveriesLoading", true);
      commit("setIsDeliveriesBlocked", true);
      try {
        const response = await Api.getDeliveries();
        response?.data && commit("setDeliveries", response.data);
      } catch (error) {
        commit("setDeliveries", []);
        commit("setPayments", []);
        showHandledMessage(this._vm, error);
      } finally {
        commit("setIsDeliveriesLoading", false);
        commit("setIsDeliveriesBlocked", false);
      }
    },
    async getPaymentsByDelivery({ commit }, { id }) {
      commit("setIsPaymentsLoading", true);
      commit("setIsPaymentsBlocked", true);
      try {
        const response = await Api.getPaymentsById(id);
        response?.data && commit("setPayments", response.data);
      } catch (error) {
        showHandledMessage(this._vm, error);
        throw error;
      } finally {
        commit("setIsPaymentsLoading", false);
        commit("setIsPaymentsBlocked", false);
      }
    },

    async getOrderInfoById({ commit }, { id }) {
      commit("setOrderInfoLoading", true);
      try {
        const response = await Api.getOrderById(id);
        response?.data && commit("setOrderInfo", response.data);
      } finally {
        commit("setOrderInfoLoading", false);
      }
    },
  },
};
