import { Messages } from "@/config";

export default function showHandledMessage(context, error) {
  switch (error?.code) {
    case "ORDER":
      context.$notify({
        text: Messages.ORDER_SUCCESS,
        type: "success",
      });
      break;
    case "PRODUCT_ADD":
      context.$notify({
        text: Messages.PRODUCT_SUCCESS,
        type: "success",
      });
      break;
    case "PRODUCT_DELETE":
      context.$notify({
        text: Messages.PRODUCT_DELETED,
        type: "warn",
      });
      break;
    case "ERR_NETWORK":
      context.$notify({
        text: Messages.ERROR_NETWORK,
        type: "error",
      });
      break;
    case "ERR_BAD_REQUEST":
      context.$notify({
        text: Messages.ERROR_BAD_REQUEST,
        type: "error",
      });
      break;
    default:
      context.$notify({
        text: Messages.ERROR_DEFAULT,
        type: "error",
      });
  }
}
