import { API_DELAY, BASE_API_URL } from "@/config";
import axios from "axios";

export default class Api {
  static axiosInstance = axios.create({
    baseURL: BASE_API_URL,
    headers: "accept: application/json",
  });
  static _cache = {};
  static _userAccessKey = null;

  static async getColors() {
    return Api._cacheIt("colors-", () =>
      Api._handleDelayedResponse(() => Api.axiosInstance.get(`/api/colors`))
    );
  }

  static async getDeliveries() {
    return Api._cacheIt("deliveries-", () =>
      Api._handleDelayedResponse(() => Api.axiosInstance.get(`/api/deliveries`))
    );
  }

  static async getMaterials() {
    return Api._cacheIt("materials-", () =>
      Api._handleDelayedResponse(() => Api.axiosInstance.get(`/api/materials`))
    );
  }

  static async createOrder({
    phone,
    name,
    address,
    email,
    deliveryTypeId,
    paymentTypeId,
    comment,
  }) {
    Api._checkAccessKeyLSExist();
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance.post(
        `/api/orders`,
        { phone, name, address, email, deliveryTypeId, paymentTypeId, comment },
        {
          params: {
            userAccessKey: Api._userAccessKey,
          },
        }
      )
    );
  }

  static async getOrderById(orderId) {
    Api._checkAccessKeyLSExist();
    return Api._cacheIt("order-by-" + orderId, () =>
      Api._handleDelayedResponse(() =>
        Api.axiosInstance.get(`/api/orders/${orderId}`, {
          params: {
            userAccessKey: Api._userAccessKey,
          },
        })
      )
    );
  }

  static async getPaymentsById(deliveryTypeId) {
    return Api._cacheIt("payments-by-" + deliveryTypeId, () =>
      Api._handleDelayedResponse(() =>
        Api.axiosInstance.get(`/api/payments`, {
          params: {
            deliveryTypeId,
          },
        })
      )
    );
  }

  static async getProductCategories() {
    return Api._cacheIt("categories-", () =>
      Api._handleDelayedResponse(() =>
        Api.axiosInstance.get(`/api/productCategories`)
      )
    );
  }

  static async getProducts({
    categoryId = 0,
    materialIds = [],
    seasonIds = [],
    colorIds = [],
    page = 1,
    limit = 12,
    minPrice = 0,
    maxPrice = 0,
  } = {}) {
    return Api._cacheIt(
      "products-",
      () =>
        Api._handleDelayedResponse(() =>
          Api.axiosInstance.get(`/api/products`, {
            params: {
              categoryId,
              materialIds,
              seasonIds,
              colorIds,
              page,
              limit,
              minPrice,
              maxPrice,
            },
          })
        ),
      categoryId,
      materialIds,
      seasonIds,
      colorIds,
      page,
      limit,
      minPrice,
      maxPrice
    );
  }

  static async getProductById(productId) {
    return Api._cacheIt("product-" + productId, () =>
      Api._handleDelayedResponse(() =>
        Api.axiosInstance.get(`/api/products/${productId}`)
      )
    );
  }

  static async getSeasons() {
    return Api._cacheIt("seasons-", () =>
      Api._handleDelayedResponse(() => Api.axiosInstance.get(`/api/seasons`))
    );
  }

  static async getCartProducts() {
    Api._checkAccessKeyLSExist();
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance.get(`/api/baskets`, {
        params: {
          userAccessKey: Api._userAccessKey,
        },
      })
    );
  }

  static async createCartProduct({ productId, colorId, sizeId, amount }) {
    Api._checkAccessKeyLSExist();
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance.post(
        `/api/baskets/products`,
        {
          productId,
          colorId,
          sizeId,
          quantity: amount,
        },
        {
          params: {
            userAccessKey: Api._userAccessKey,
          },
        }
      )
    );
  }

  static async updateCartProductAmount({ itemId, newAmount }) {
    Api._checkAccessKeyLSExist();
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance.put(
        `/api/baskets/products`,
        {
          basketItemId: itemId,
          quantity: newAmount,
        },
        {
          params: { userAccessKey: Api._userAccessKey },
        }
      )
    );
  }

  static async deleteCartProduct(itemId) {
    Api._checkAccessKeyLSExist();
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance.delete(`/api/baskets/products`, {
        data: {
          basketItemId: itemId,
        },
        params: {
          userAccessKey: Api._userAccessKey,
        },
      })
    );
  }

  static async getUserAccessKey() {
    return Api._handleDelayedResponse(() =>
      Api.axiosInstance(`/api/users/accessKey`)
    );
  }

  static async _checkAccessKeyLSExist() {
    const userAccessKey = localStorage.getItem("user-access-key");
    if (userAccessKey) {
      Api._userAccessKey = userAccessKey;
    } else {
      const response = await Api.getUserAccessKey();
      if (response.status === 200 && response?.data?.accessKey) {
        localStorage.setItem("user-access-key", response.data.accessKey);
        Api._userAccessKey = response.data.accessKey;
      }
    }
  }

  static async _handleDelayedResponse(func) {
    return new Promise((res, rej) => {
      setTimeout(async () => {
        try {
          const response = await func();
          res(response);
        } catch (err) {
          rej(err);
        }
      }, API_DELAY);
    });
  }

  static async _cacheIt(prefix, func, ...args) {
    const cacheName = prefix + JSON.stringify(args);
    try {
      if (!Api._cache[cacheName]) {
        Api._cache[cacheName] = await func(...args);
        return Api._cache[cacheName];
      } else {
        return Api._cache[cacheName];
      }
    } catch (error) {
      delete Api._cache[cacheName];
      throw error;
    }
  }
}
