<template>
  <div>
    <BaseHeader />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'home' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> Оформление заказа </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен
          <span v-if="orderInfo?.id">№ {{ orderInfo.id }} </span>
        </h1>
      </div>

      <section class="cart with-preloader">
        <form
          class="cart__form form"
          action="#"
          method="POST"
          v-if="orderInfo?.id"
        >
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту
              придет письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся
              с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
            </p>

            <ul class="dictionary" v-if="orderInfo">
              <li class="dictionary__item">
                <span class="dictionary__key"> Получатель </span>
                <span class="dictionary__value">
                  {{ orderInfo.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Адрес доставки </span>
                <span class="dictionary__value">
                  {{ orderInfo.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Телефон </span>
                <span class="dictionary__value">
                  {{ orderInfo.phone | phoneNumberFormat }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Email </span>
                <span class="dictionary__value"> {{ orderInfo.email }} </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Способ оплаты </span>
                <span class="dictionary__value">
                  {{ orderInfo.paymentType }}
                </span>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <CartOrderList :products="orderInfoProducts" />
            <CartTotal
              :total-price="orderInfo.totalPrice"
              :delivery-title="orderInfoDelivery.title"
              :delivery-price="orderInfoDelivery.price"
              :amount="orderInfoProductsCount"
            />
          </div>
        </form>
        <BasePreloader v-if="orderInfoLoading" />
      </section>
    </main>
    <BaseFooter />
  </div>
</template>

<script>
import BaseFooter from "@/components/BaseFooter.vue";
import BaseHeader from "@/components/BaseHeader.vue";
import BasePreloader from "@/components/BasePreloader.vue";
import CartOrderList from "@/components/CartOrderList.vue";
import CartTotal from "@/components/CartTotal.vue";
import numberFormat from "@/helpers/number-format";
import normalizeWord from "@/helpers/normalise-word";
import phoneNumberFormat from "@/helpers/phone-format";
import { mapActions } from "vuex";

export default {
  name: "OrderInfoPage",
  components: {
    BaseHeader,
    BaseFooter,
    BasePreloader,
    CartOrderList,
    CartTotal,
  },
  data() {
    return {
      isLoading: true,
    };
  },
  filters: {
    numberFormat,
    normalizeWord,
    phoneNumberFormat,
  },
  methods: {
    ...mapActions(["getOrderInfoById"]),
  },
  watch: {
    "$route.params.id": {
      immediate: true,
      handler: function (newOrderId) {
        if (this.orderInfo && this.orderInfo?.id === Number(newOrderId)) {
          return;
        }

        this.getOrderInfoById({
          id: newOrderId,
        }).catch(() => {
          this.$router.replace({ name: "notFound" });
        });
      },
    },
  },
};
</script>
